.toggle-button{
    width: 50px;
    height: 25px;
    border-radius: 20px;
    border:1px solid #999;
    background-color: white;
    position: fixed;
    top: 10px;
    right: 90px;
    z-index: 999;
    display: flex;
    justify-content: space-around;

}

.t-button{
    width: 25px;
    height: 25px;
    border-radius: 50%;
    background-color: #999;
    position: absolute;
    cursor: pointer;
    right:25px;
}

@media only screen and (max-width: 768px) {
    .toggle-button{
        display: none;
    }
}